<template>

  <div>

    <!-- Submit Assignment Modal-->

<div
        class="modal fade"
        id="SubmitAssignmentModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Submit Assignment
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="SubmitResponse">
                  <div class="form-group">
                    <label for="acode">Assignment Code</label>
                  </div>
                   <div class="form-group">
                    <p>{{NewAcode}}</p>
                  </div> 
                  <div class="form-group">
                    <label>Assignment Heading</label>
                                      </div>
                  <div class="form-group">
                    <p>{{NewAssignmenthead}}</p>
                  </div>
                  <div class="form-group">
                    <label for="alink">Add Assignment Response</label>
                    <input
                      type="text"
                      id="alink"
                      name="alink"
                      placeholder="Enter Assignment Response"
                      v-model="NewAssignmentlink"
                      class="form-control"
                    />
                  </div>
                                    <div class="form-group">
                    <label for="alink">Any comments</label>
                    <input
                      type="text"
                      id="alink"
                      name="alink"
                      placeholder="Enter Assignment Response"
                      v-model="comments"
                      class="form-control"
                    />
                  </div>

                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal">Submit Response</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>




<div class="mt-2">
<div class="table-responsive mt-4">
    <table class="table table-striped table-sm">
        <thead>
            <tr class="text-center fw-bold">
            <th scope="col">A Code</th>
            <th scope="col">Assignment Heading</th>
            <th scope="col">Assignment Desc</th>
            <th scope="col">Assignment Link</th>
            <th scope="col">Submit</th>
            <!-- <th scope="col">Delete</th> -->
            </tr>
            </thead>
            <tbody class="text-center ">
                <tr class="mt-2" v-for="(assignment, _id) in Assignments" :key="_id">

                <td>{{assignment.acode}}</td>
                <td>{{assignment.aheading}}</td>
                <td>{{assignment.adesc}}</td>
                <td><a :href="assignment.alink" target="_blank"><button class="btn btn-info">Open Link</button></a></td>

                <td><button class="btn btn-primary"  data-bs-toggle="modal"
      data-bs-target="#SubmitAssignmentModal" @click="submitAssignment(_id)">Submit</button></td>
                <!-- <td><button class="btn btn-danger" @click="deleteAssignment(_id)">Delete</button></td> -->
                
                </tr>
                
            </tbody>
    </table>
</div>
</div>

</div>

</template>

<script>
import { mapActions,mapGetters } from "vuex";
import axios from 'axios'
import config from "@/config"

export default {
name:'ViewAssignment',
data(){
return{

   acode:"",
    aheading: "",
    adesc:"",
  alink:"" ,
  comments:"",

  Assignments:[],

  //update form data
  NewAcode:'',
  NewAssignmenthead:'',
  NewAssignmentlink:''
}

},
computed:{
    ...mapGetters(["user"]),
},
methods:{
  // get assignment lists
async getAssignments(){
  
    let {
    data
  } = await axios.get(`${config.apiBaseUrl}/api/assignments/get-assignments`)
  console.log(this.Assignments = data.reverse())
 

},

 ...mapActions(["getProfile"]),
// Submit Assignment
submitAssignment(_id){
  this.NewAcode = this.Assignments[_id].acode
  this.NewAssignmenthead = this.Assignments[_id].aheading
  this.NewAssignmentlink = this.Assignments[_id].alink

},
 async SubmitResponse(){
  let assignment = {
    acode:this.NewAcode,
    aheading:this.NewAssignmenthead,
    username:this.user.username,
    aslink: this.NewAssignmentlink,
      comments:this.comments
  }
let res = await axios.post(`${config.apiBaseUrl}/api/submissions/submit-assignment`,assignment)
  this.$toasted.show('Assignment Submitted');
      if(res.data.success){
        this.getAssignments()
        }
}


},

created(){
  this.getAssignments()
      this.getProfile();
}

}
</script>

<style scoped>

label{
  font-weight: 600;
}

</style>